html,body,div,header,section,footer,h1,h3,img,a,dl,dd,dt,ul,li,p,span{
  margin:0;padding:0
}
body{
  width:100%;
  background-color:#eee;
  color:#222;
  font-family:Futura,Trebuchet MS,Arial,sans-serif;
  font-size:18px;
  -webkit-transition:-webkit-transform 4s ease-out;
  transition:transform 4s ease-out
}
.container{
  width:100%;
  text-align:center
}
header h1{
  font-size:112px;
  font-size:7rem;
  margin:5% 0 6%;
}
@media only screen and (max-width:760px){
  header h1{
    font-size:3rem;
    margin-top:5%;
  }
  header img{
    width:100%;
  }
}

.konamied {
  transform: rotate(180deg);
  background-image: url('http://static.blog.playstation.com/wp-content/uploads/avatars//Old-Konami-Logo.jpg?m=1414759874');
}

section {
  margin-bottom: 3em;
}

h3 {
  font-size: 2rem;
}

ul {
  list-style: none;
}

li {
  line-height: 2;
}

footer {
  margin-top: 6%;
}